<template>
  <div>
    <div style="margin: 20px;">
      <div style="margin-bottom: 10px;">
        <el-button type="primary" @click="handleOpenTerminal"></el-button>
        <el-button @click="handleReset"></el-button>
      </div>
      <el-form ref="form" :model="connectData" label-width="80px" :inline="true">
        <el-form-item prop="host" label="IP">
          <el-input v-model="connectData.host" style="width: 150px;"></el-input>
        </el-form-item>
        <el-form-item prop="port" label="">
          <el-input v-model="connectData.port" style="width: 150px;"></el-input>
        </el-form-item>
        <el-form-item prop="username" label="">
          <el-input v-model="connectData.username" autocomplete="off" style="width: 150px;"></el-input>
        </el-form-item>
        <el-form-item prop="password" label="">
          <el-input v-model="connectData.password" type="password" style="width: 150px;"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div id="webssh" style="width: 600px;"></div>
  </div>
</template>

<script>
import { WSSHClient } from "@/utils/webssh";
import { Terminal } from "xterm";
import { FitAddon } from 'xterm-addon-fit';
export default {
  name: "SSH",
  data() {
    return {
      connectData: {
        operate: 'connect',
        host: '',
        port: '22',
        username: 'root',
        password: ''
      }
    }
  },
  methods: {
    openTerminal(options){
      let client = new WSSHClient();
      const term = new Terminal({
        rendererType: 'canvas',
        cols: 97,
        rows: 37,
        cursorBlink: true, // 
        cursorStyle: "underline", //   null | 'block' | 'underline' | 'bar'
        scrollback: 10000, //
        tabStopWidth: 8, //
        screenKeys: true,
        disableStdin: false,
        theme: {
          foreground: 'yellow', //
          background: '#060101', //
          cursor: 'help',//
        }
      });
      term.onData((data) => {
        client.sendClientData(data);
      })
      let fitAddon = new FitAddon();
      term.loadAddon(fitAddon);
      term.open(document.querySelector("#webssh"));
      term.write("Connecting....\r\n");
      client.connect({
        onError: (error) => {
          term.write('Error: ' + error + '\r\n');
        },
        onConnect: () => {
          client.sendInitData(options);
        },
        onClose: () => {
          term.write("\rconnection closed");
        },
        onData: (data) => {
          if(data.indexOf('\r\nlogout') > -1){
            this.$message.error('!');
          }
          term.write(data);
        }
      })
    },
    handleOpenTerminal(){
      this.openTerminal(this.connectData);
    },
    handleReset(){
      this.$refs['form'].resetFields();
    }
  }
}
</script>

<style>
  .xterm-helper-textarea{
    opacity: 0;
  }
  .xterm-char-measure-element{
    opacity: 0;
  }
</style>
